// Header : 상단 메뉴 //
import React from 'react';
import { Link } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import { IoMenu } from 'react-icons/io5';
import styled from 'styled-components';
import '../css/fonts.css';

// style //
const HeaderBlock = styled.div`
    width: 100%;
    height: 90px;
    position: fixed;
    display: flex;
    margin-bottom: 100px;
    background: #FFFFFF;
    box-shadow: 2px 2px 5px rgba(0,0,0,.06);
    font-family: "rocket";
    color: #2B2B2B;
    z-index: 1;

    .link {
        margin: auto 0;
        color: #2B2B2B;
    }

    h1 {
        margin: auto 0px auto 30px;
        border-radius: 5% 40%;
        background: #F3E9F5;
        font-size: 35px;
    }

    .menu-icon {
        position: absolute;
        top: 20px;
        right: 40px;
        font-size: 55px;
        color: #2B2B2B;
        
        &:hover {
            cursor: pointer;
            color: #8B8B8B;
        }
    }
`;

const Menu = styled.div`
    width: 100%;
    position: relative;
`;

const MenuGroup = styled.div`
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    display: flex;
    align-items: center;

    li {
        width: 120px;
        height: 60px;
        margin-right: 25px;
        list-style: none;
        font-size: 21px;
        text-align: center;
        line-height: 60px;

        &:hover {
            cursor: pointer;
            transition: 0.5s;
            color: #877ACD;
        }
    }

    li:last-child { margin-right: 50px; }
`;

const ToggleMenu = styled.div`
    width: 110%;
    display: none;
    position: absolute;
    top: 90px;
    left: 15px;
    background: #BFA2DB;
    
    ul {
        display: flex;
        justify-content: center;
        color: #FFFFFF;
    }

    ul:last-child { margin-top: 0px; }

    li {
        padding: 0px 50px;
        border-right: 2px solid #FFFFFF;
        font-family: "bath_bold";
        font-size: 25px;
    }

    li:last-child { border: none; }

    @media screen and (max-width: 600px) {
        left: 25px;
    }

    @media screen and (max-width: 500px) {
        width: 113%;
        ul { margin-left: -30px; }
        li { font-size: 20px; }
    }
`;

// function //
function Header() {
    const onClickMenu = (menuname) => {
        let location;
        if(menuname === "home")
            location = document.getElementById("home-section").offsetTop;
        else if(menuname === "about")
            location = document.getElementById("about-section").offsetTop-80;
        else if(menuname === "skill")
            location = document.getElementById("skill-section").offsetTop-80;
        else if(menuname === "portfolio")
            location = document.getElementById("portfolio-section").offsetTop-80;
        else if(menuname === "contact")
            location = document.getElementById("contact-section").offsetTop-80;

        window.scrollTo({top: location, left: 0, behavior: 'smooth'});
    };

    const onClickToggleMenu = () => {
        let toggle = document.getElementById("toggle-menu").style;
        if(toggle.display === "block")
            toggle.display = "none";
        else
            toggle.display = "block";
    };

    const isPC = useMediaQuery({
        query : "(min-width: 768px)"
    });

    const isMobile = useMediaQuery({
        query : "(max-width: 768px)"
    });

    return(
        <>
            {isMobile && 
                <>
                    <HeaderBlock>
                        <Link to="/" className="link"><h1>YOUNG.</h1></Link>
                        <IoMenu className="menu-icon" onClick={onClickToggleMenu}/>
                    </HeaderBlock>
                    <ToggleMenu id="toggle-menu">
                        <ul>
                            <li onClick={()=>onClickMenu("home")}>Home</li>
                            <li onClick={()=>onClickMenu("about")}>About</li>
                            <li onClick={()=>onClickMenu("skill")}>Skill</li>
                        </ul>
                        <ul>
                            <li onClick={()=>onClickMenu("portfolio")}>Portfolio</li>
                            <li onClick={()=>onClickMenu("contact")}>Contact</li>
                        </ul>
                    </ToggleMenu>
                </>
            }
            {isPC &&
                <HeaderBlock>
                    <Link to="/" className="link"><h1>YOUNG.</h1></Link>
                    <Menu>
                        <MenuGroup>
                            <li onClick={()=>onClickMenu("home")}>Home</li>
                            <li onClick={()=>onClickMenu("about")}>About</li>
                            <li onClick={()=>onClickMenu("skill")}>Skill</li>
                            <li onClick={()=>onClickMenu("portfolio")}>Portfolio</li>
                            <li onClick={()=>onClickMenu("contact")}>Contact</li>
                        </MenuGroup>
                    </Menu>
                </HeaderBlock>
            }
        </>
    );
}

export default Header;