/* Project : 프로젝트 상세 설명 페이지 */
import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import { Swiper, SwiperSlide } from 'swiper/react';
import "swiper/swiper.min.css";
import "swiper/components/effect-coverflow/effect-coverflow.min.css";
import "swiper/components/pagination/pagination.min.css";

import SwiperCore, {
    EffectCoverflow,Pagination
} from 'swiper/core';
  
SwiperCore.use([EffectCoverflow,Pagination]);

// style //
const ProjectBlock = styled.div`
    h1 {
        margin: 80px 0px 20px 0px;
        padding-bottom: 5px;
        font-family: "bath_bold";
        font-size: 35px;
        letter-spacing: 1px;
        text-align: center;
        color: #3B3B3B;
    }
`;

const Slider = styled.div`
    height: 550px;
    position: relative;
    z-index: 0;

    .swiper-container {
        width: 100%;
        height: 95%;
        padding: 20px;
    }

    .swiper-slide {
        width: 60%;
        height: 500px;
        background-position: center;
        background-size: cover;
    }

    img {
        width: 100%;
        height: 100%;
    }

    @media screen and (max-width: 1000px) {
        .swiper-slide {
            width: 70%;
        }
    }

    @media screen and (max-width: 768px) {
        height: 460px;

        .swiper-slide {
            width: 80%;
            height: 400px;
        }
    }
`;

const Description = styled.div`
    margin: 50px 120px 100px 120px;

    .project-content {
        display: flex;
        margin-bottom: 20px;
    }
    
    h6 { margin: 0; }

    .title {
        font-family: "bath_bold";
        font-size: 18px;
    }

    .title-period { margin-right: 100px; }

    .title-language { margin-right: 70px; }

    .title-skill { margin-right: 110px; }

    .title-contribution { margin-right: 40px; }

    .title-description { margin-right: 50px; }

    .title-function { margin-right: 70px; }

    .answer {
        font-family: "bath_light";
        font-size: 18px;
    }

    ul {
        width: 80%;
        margin: 0;
        padding: 20px 0px;
        background: #EEEEEE;
    }

    li {
        margin-bottom: 10px;
        font-family: "bath_medium";
        font-size: 18px;
    }

    .diagram {
        width: 70%;
        height: 600px;
        background: pink;
        margin-bottom: 20px;
    }

    .button-group {
        display: flex;
        justify-content: center;
        margin-top: 50px;
    }

    .demo-button {
        width: 150px;
        margin-right: 50px;
        padding: 12px 20px;
        background: #a294d2;
        font-family: "bath_bold";
        text-align: center;
        color: #FFFFFF;
        
        &:hover {
            cursor: pointer;
            filter: brightness(97%);
        }
    }

    .home-button {
        width: 150px;
        padding: 12px 20px;
        background: #7364b9;
        font-family: "bath_bold";
        text-align: center;
        color: #FFFFFF;
        
        &:hover {
            cursor: pointer;
            filter: brightness(97%);
        }
    }
`;

// function //
function Project(props) {
    const { name, period, language, skill, contribution, description, functions, capturedImage, urlLink } = props.projects;

    return (
        <ProjectBlock>
            <h1 className="project-name">{name}</h1>
            <Slider>
                <Swiper effect={'coverflow'} grabCursor={true} centeredSlides={true} slidesPerView={'auto'} coverflowEffect={{
                        "rotate": 50, "stretch": 0, "depth": 100, "modifier": 1, "slideShadows": true}}
                        pagination={true} className="swiper-container">
                    <SwiperSlide className="swiper-slide"><img src={capturedImage[0]} alt=""/></SwiperSlide>
                    <SwiperSlide className="swiper-slide"><img src={capturedImage[1]} alt=""/></SwiperSlide>
                    <SwiperSlide className="swiper-slide"><img src={capturedImage[2]} alt=""/></SwiperSlide>
                    <SwiperSlide className="swiper-slide"><img src={capturedImage[3]} alt=""/></SwiperSlide>
                    <SwiperSlide className="swiper-slide"><img src={capturedImage[4]} alt=""/></SwiperSlide>
                </Swiper>
            </Slider>
            <Description>
                <div className="project-content">
                    <h6 className="title title-period">Period</h6>
                    <h6 className="answer">{period}</h6>
                </div>
                <div className="project-content">
                    <h6 className="title title-language">Language</h6>
                    <h6 className="answer">{language}</h6>
                </div>
                <div className="project-content">
                    <h6 className="title title-skill">Skill</h6>
                    <ul>
                        <li>{skill[0]}</li>
                        <li>{skill[1]}</li>
                        <li>{skill[2]}</li>
                    </ul>
                    
                </div>
                <div className="project-content">
                    <h6 className="title title-contribution">Contribution</h6>
                    <h6 className="answer">{contribution}</h6>
                </div>
                <div className="project-content">
                    <h6 className="title title-description">Description</h6>
                    <h6 className="answer">{description}</h6>
                </div>
                <div className="project-content">
                    <h6 className="title title-function">Function</h6>
                    <ul>
                        <li>{functions[0]}</li>
                        <li>{functions[1]}</li>
                        <li>{functions[2]}</li>
                        <li>{functions[3]}</li>
                        <li>{functions[4]}</li>
                        <li>{functions[5]}</li>
                    </ul>
                </div>
                <div className="button-group">
                    <a href={urlLink} target="_blank"><div className="demo-button">Live Demo</div></a>
                    <Link to = "/"><div className="home-button">Home</div></Link>
                </div>
            </Description>       
        </ProjectBlock>
    );
}

export default Project;